import React from "react";
import Banner from "../components/banner";
function PrivacyPolicy() {
  return (
    <div className="contents pages">
      <Banner title="Privacy Policy" />
      <div className="ct">
        <div className="hdr">Privacy Policy</div>
        <div className="small-hdr"> </div>
        <div className="para">
          We make every effort to protect the privacy of our users and
          customers. As such, this privacy policy sets out how M/S Sooti Studio
          (hereafter referred to as 'us' or 'we') uses and protects any
          information that you give us when you use this website. Your online
          privacy and security are our priority, and we are committed to
          ensuring that you are protected.
        </div>
        <div className="para">
          Sooti Studio operates the https://www.sootistudio.com website, which
          provides the services of ecommerce. This page is used to inform
          website visitors regarding our policies with the collection, use, and
          disclosure of Personal Information if anyone decided to use our
          Service. If you choose to use our Service, then you agree to the
          collection and use of information concerning this policy. The Personal
          Information that we collect is used for providing and improving the
          Service. We will not use or share your information with anyone except
          as described in this Privacy Policy. The terms used in this Privacy
          Policy have the same meanings as in our Terms and Conditions, which is
          accessible unless otherwise defined in this Privacy Policy.
        </div>
        <div className="small-hdr">Information Collection and Use</div>
        <div className="para">
          For a better experience, while using our Service, we may require you
          to provide us with certain personally identifiable information,
          including but not limited to your name, phone number, and postal
          address. The information that we collect will be used to contact or
          identify you.
        </div>
        <div className="small-hdr">Log Data</div>
        <div className="para">
          We want to inform you that whenever you visit our Service, we collect
          information that your browser sends to us which is called Log Data.
          This Log Data may include information such as your computer’s Internet
          Protocol (“IP”) address, browser version, pages of our Service that
          you visit, the time and date of your visit, the time spent on those
          pages, and other statistics.
        </div>
        <div className="small-hdr">Cookies</div>
        <div className="para">
          Cookies are files with a small amount of data that is commonly used as
          an anonymous unique identifier. These are sent to your browser from
          the website that you visit and are stored on your computer’s hard
          drive.
        </div>
        <div className="para">
          Our website uses these “cookies” to collect information and to improve
          our Service. You have the option to either accept or refuse these
          cookies and know when a cookie is being sent to your computer. If you
          choose to refuse our cookies, you may not be able to use some portions
          of our Service.
        </div>

        <div className="small-hdr">Service Providers</div>
        <div className="para">
          We may employ third-party companies and individuals due to the
          following reasons:
        </div>
        <div className="para">To facilitate our Service;</div>
        <div className="para">To provide the Service on our behalf;</div>
        <div className="para">To perform Service-related services; or</div>
        <div className="para">
          To assist us in analyzing how our Service is used
        </div>
        <div className="para">
          We want to inform our Service users that these third parties have
          access to their Personal Information. The reason is to perform the
          tasks assigned to them on our behalf. However, they are obligated not
          to disclose or use the information for any other purpose.
        </div>
        <div className="small-hdr">Security</div>
        <div className="para">
          We value your trust in providing us your Personal Information, thus we
          are striving to use commercially acceptable means of protecting it.
          But remember that no method of transmission over the internet, or
          method of electronic storage is 100% secure and reliable, and we
          cannot guarantee its absolute security.
        </div>
        <div className="small-hdr">Links to Other Sites</div>
        <div className="para">
          Our Service may contain links to other sites. If you click on a
          third-party link, you will be directed to that site. Note that these
          external sites are not operated by us. Therefore, we strongly advise
          you to review the Privacy Policy of these websites. We have no control
          over and assume no responsibility for the content, privacy policies,
          or practices of any third-party sites or services.
        </div>

        <div className="small-hdr">Children’s Privacy</div>
        <div className="para">
          Our Services do not address anyone under the age of 18. We do not
          knowingly collect personally identifiable information from children
          under 18. In the case we discover that a child under 18 has provided
          us with personal information, we immediately delete this from our
          servers. If you are a parent or guardian and you are aware that your
          child has provided us with personal information, please contact us so
          that we will be able to do the necessary actions.
        </div>

        <div className="small-hdr">Changes to This Privacy Policy</div>
        <div className="para">
          We may update our Privacy Policy from time to time. Thus, we advise
          you to review this page periodically for any changes. We will notify
          you of any changes by posting the new Privacy Policy on this page.
          These changes are effective immediately after they are posted on this
          page.
        </div>
        <div className="small-hdr">Contact Us</div>
        <div className="para">
          If you have any questions or suggestions about our Privacy Policy, do
          not hesitate to contact us at contact@sootistudio.com This Privacy
          Policy page was created at https://www.sootistudio.com
        </div>
      </div>
    </div>
  );
}

export default PrivacyPolicy;
